@font-face {
  src: local("Open Sans"), local("OpenSans"), url("../res/fonts/DINCE-BoldAlternate.otf") format("embedded-opentype"), url("../res/fonts/DINCE-BoldAlternate.otf") format("embedded-opentype"), url("../res/fonts/DINCE-BoldAlternate.otf") format("embedded-opentype"), url("../res/fonts/DINCE-BoldAlternate.otf") format("embedded-opentype"), url("../res/fonts/DINCE-BoldAlternate.otf") format("embedded-opentype"); }

.bigLightTitle {
  color: #f6f7f9;
  font-weight: 600;
  font-size: 50px; }

.bigDarkTitle {
  color: #3a3a3a;
  font-weight: 600;
  font-size: 28px; }

.mediumLightTitle {
  color: #f6f7f9;
  font-weight: 400;
  font-size: 20px; }

.mediumDarkTitle {
  color: #3a3a3a;
  font-weight: 500;
  font-size: 20px; }

.mediumRedTitle {
  color: #c51e2b;
  font-weight: 400;
  font-size: 30px;
  line-height: 1.3; }

.smallDarkTitle {
  color: #3a3a3a;
  font-weight: 600;
  font-size: 15px; }

.lightParagraph {
  color: #f6f7f9;
  font-weight: 300;
  font-size: 20px; }

.lightBoldParagraph {
  color: #f6f7f9;
  font-weight: 500;
  font-size: 20px; }

.darkParagraph {
  color: #3a3a3a;
  font-weight: 300;
  font-size: 20px;
  line-height: 1.4; }

*, ::after, ::before {
  box-sizing: unset !important; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background-color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }
