.topWrapper {
  position: relative;
  background-image: url("../res/img/svg/space_zgoraj_kvadrat.svg");
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  overflow: hidden; }
  .topWrapper .topParallaxWrapper {
    background-color: black;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 20px;
    pointer-events: none; }
  .topWrapper .shortParallax {
    height: calc(100vh - 70vh);
    position: relative; }
    .topWrapper .shortParallax .innerParallax {
      transition: transform 0.1s ease-out;
      height: calc(100vh - 70px); }
  .topWrapper .topParallax {
    height: calc(100vh - 70px);
    position: relative; }
    .topWrapper .topParallax .innerParallax {
      transition: transform 0.1s ease-out;
      height: calc(100vh - 70px);
      position: relative; }
      .topWrapper .topParallax .innerParallax .parallaxCenter {
        position: absolute;
        top: calc(50% - 70px);
        transform: translate(0, -50%); }
  .topWrapper #waveCover {
    /*position:absolute;
        bottom:-10px;*/
    position: relative;
    width: 100vw;
    z-index: 3000;
    padding-bottom: -20px;
    overflow: hidden; }
    .topWrapper #waveCover .svg-wave {
      width: 100vw; }

#bottom_parallax {
  margin-top: -10px;
  background: #f2f2f2;
  z-index: 4000;
  position: relative; }
